import Top from "../../components/public/title";
import Footer from "../../components/public/footer_3.2";
import Step1 from "../../components/info/step1";
import Step2 from "../../components/info/step2";
import Step3 from "../../components/info/step3";
import Step2_1 from "../../components/info/step2_1";
import "./style.less";
import $ from "jquery";
import url from "../../config/config"
// import store from "../../../store/store";
export default React.createClass({
    getInitialState(){
        return {
            page:1,
            // creditBank:"1"
        }
    },
    submit(){
    },
    
    componentWillMount(){
        // console.log("")
        var that=this;
        $.ajax({
            type: "get",
            url: url.url+"/api/act/mine/userInfo/getUserInfo.htm",
            data: {userId:localStorage.userId},
            dataType: "json",
            headers:{"Content-Type":"text/plain;charset=UTF-8",token:localStorage.Token},
            success: function (data) {
                switch(data.code){
                    case 408:    Toast.info('', 1);
                                    break;
                    case 411:    Toast.info('', 1);
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    hashHistory.push("login");
                                    break;
                    case 410:    Toast.info('', 1);
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    hashHistory.push("login");
                                    break;
                    case 500:    Toast.info('', 1);
                                    break;
                    case 170002:    Toast.info('', 1);
                                    break;
                    case 170003:    Toast.info('', 1);
                                    break;
                    default:        break;
                }     
                    that.setState(data.data);

            }
        });
        if(sessionStorage.info){
            this.setState(JSON.parse(sessionStorage.info))
        }
    },
    componentWillUnmount(){

        sessionStorage.info=JSON.stringify(this.state);
    },
    btn(w,rrr){
        this.setState({
            page:rrr
        })
        
    },
   
    render:function(){
        var content=null;
        if(this.state.page==1){
            content=<Step1 ddd={(w,eee)=>{this.btn(w,eee)}} />
        }else if(this.state.page==2){
            content=<Step2 step={(w)=>{this.setState({page:w})}} />
        }else if(this.state.page==3){
            content=<Step3 />
        }
        return (
            <div className="information">
                <Top back={true} title="" />
                <Step1 page={this.state.page} step={(w)=>{
                    window.scrollTo(0,0);
                    this.setState({page:w})}} ref="step_1"
                    data1={this.state} />
                <Step2 page={this.state.page} step={(w)=>{
                    window.scrollTo(0,0);
                    this.setState({page:w})}}  
                    data2={this.state}/>
                <Step2_1 page={this.state.page} step={(w)=>{
                    window.scrollTo(0,0);
                    this.setState({page:w})}}  
                    data2={this.state}/>
                <Step3 page={this.state.page} step={(w)=>{
                    window.scrollTo(0,0);
                    this.setState({page:w})}} submit={this.submit} changepage={()=>{
                    this.setState({page:1})
                    // console.log(1)
                }} 
                
                data3={this.state}/>
                <Footer />
            </div>
        )
    }
})


