export default React.createClass({
    render(){
        return (
            <div className="banner">
                <img src="images/homeimages/bzbanner.gif"/>
                <div className="bzword">
                  <p className="bzspana"></p>
                  <p className="bzspanc"><span className="bzspanb">20</span></p>
                </div>
            </div>
        )
    }
})


