import Top from "../../components/public/title";
import Footer from "../../components/public/footer_3";
import "./style.less";
import Con from "../../components/creditcard/content";
export default React.createClass({
    render(){
        return (
            <div className="card">
                <Top title="" back="true" />
                <Con />
                <Footer home="true" />
            </div>
        ) 
    }
})


