import {List,Toast} from "antd-mobile";
const Item=List.Item;
const Brief = Item.Brief;
import $ from "jquery";
import url from "../../config/config";
import {hashHistory} from "react-router";
export default React.createClass({
    getInitialState(){
        return {
            
        }
    },
    componentWillMount(){
        var that=this;
        $.ajax({
            type: "get",
            url: url.url+"/api/act/mine/userInfo/getUserInfo.htm",
            data: {userId:localStorage.userId},
            dataType: "json",
            headers:{"Content-Type":"text/plain;charset=UTF-8",token:localStorage.Token},
            success: function (data) {
                switch(data.code){
                    case 408:    Toast.info('', 1);
                                    break;
                    case 411:    Toast.info('', 1);
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    hashHistory.push("login");
                                    break;
                    case 410:    Toast.info('', 1);
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    hashHistory.push("login");
                                    break;
                    case 500:    Toast.info('', 1);
                                    break;
                    case 170002:    Toast.info('', 1);
                                    break;
                    case 170003:    Toast.info('', 1);
                                    break;
                    default:        break;
                }     
                // if(data.code=="200"){
                    that.setState(data.data)
                // }
            }
        });
    },
    render(){
        return (
            <div className="list">
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                    ></i>
                    
                </div>
                <div className="list_item">
                        <div>
                            <span></span>
                            <span>{this.state.realName}</span>
                        </div>
                        <div className="border_item">
                            <span></span>
                            <span>{this.state.phone}</span>
                        </div>
                        <div className="border_item">
                            <span></span>
                            <span>{this.state.email}</span>
                        </div>
                </div>
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                    ></i>
                    
                </div>
                <div className="list_item_2">
                        <div>
                            <span></span>
                            <span>{this.state.idNo}</span>
                        </div>
                        <div className="border_item">
                            <span></span>
                        </div>
                        <div className="img_item">
                            <div className="left">
                                <div>
                                    <img src={this.state.userFrontImg}/>
                                </div>
                                <p></p>
                            </div>
                            <div className="center">
                                <div>
                                    <img src={this.state.userBackImg}/>
                                </div>
                                <p></p>
                            </div>
                            <div className="right">
                                <div>
                                    <img src={this.state.userLivingImg}/>
                                </div>
                                <p></p>
                            </div>
                        </div>
                </div>
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                    ></i>
                    
                </div>
                <div className="list_item">
                        <div>
                           <span></span>
                            <span>{this.state.bank}</span>
                        </div>
                        <div className="border_item">
                           <span></span>
                            <span>{this.state.cardNo}</span>
                        </div> 
                        <div className="border_item">
                            <span></span>
                        </div>
                        <div className="img_item">
                            <div className="left">
                                <div>
                                    <img src={this.state.frontImg}/>
                                </div>
                                <p></p>
                            </div>
                            <div className="right">
                                <div>
                                    <img src={this.state.backImg}/>
                                </div>
                                <p></p>
                            </div>
                        </div>
                </div>
                <div className="tip">
                    <i
                        style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                    ></i>
                    
                </div>
                <div className="list_item">
                        <div>
                           <span></span>
                            <span>{this.state.creditBank}</span>
                        </div>
                        <div className="border_item">
                            <span></span>
                            <span>{this.state.creditCardNo}</span>
                        </div>
                        <div className="border_item">
                            <span></span>
                        </div>
                        <div className="img_item">
                            <div className="left">
                                <div>
                                    <img src={this.state.creditFrontImg}/>
                                </div>
                                <p></p>
                            </div>
                            <div className="right">
                                <div>
                                    <img src={this.state.creditBackImg}/>
                                </div>
                                <p></p>
                            </div>
                        </div>
                </div>
                
                <div className="job_info">
                    <i
                        style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                    ></i>
                    
                </div>
                <div className="list_item">
                        <div>
                            <span></span>
                            <span>{this.state.certificateNo}</span>
                        </div>
                        <div className="border_item">
                            <span></span>
                            <span>{this.state.companyName}</span>
                        </div>
                        <div className="border_item">
                            <span></span>
                            <span>{this.state.title}</span>
                        </div>
                        <div className="border_item"
                            style={{height:"0.86rem"}}
                        >
                            <span
                                style={{width:"100%"}}
                            ></span>
                        </div>
                        <div className="img_item"
                            style={{padding:"0.28rem 0 0.7rem",height:"1.32rem"}}
                        >
                            <div className="left"
                               style={{height:"1.32rem",width:"1.92rem"}} 
                            >
                                <div>
                                    <img 
                                        style={{width:"100%",height:"100%"}}
                                        src={this.state. titleImg}
                                    />
                                </div>
                            </div>
                            
                        </div>
                </div>
                <div
                    style={{height:"0.8rem",lineHeight:"0.8rem",color:"#f89c47",borderRadius:"0.15rem",background:"#fff",paddingLeft:"0.23rem",marginTop:"0.5rem",fontSize:"0.28rem",display:this.props.info?"none":""}}
                >:400-6986967</div>
            </div>
        )
    }
})


