import {hashHistory,browserHistory} from "react-router";
import { Modal, Button, WhiteSpace, WingBlank, Toast} from 'antd-mobile';
import store from "../../../store/store";
import url from "../../config/config";
const alert = Modal.alert;
export default React.createClass({
	componentWillMount(){
		// console.log(url)
	},
	componentWillReceiveProps(newtprops){
		
		this.setState(newtprops)
	},
	btn(){
		
		if(localStorage.Login){//
			if(this.state.info==""){//
				hashHistory.push("information");
			}else if(this.state.info==""||this.state.info==""||this.state.info==""||this.state.info==""){
				
				hashHistory.push("withdraw");
				sessionStorage.withdraw="";
			}
		}else{
			hashHistory.push("login")
		}
	},
	render(){
		return (
		<div>
		<div 
		style={{background:"url(images/homeimages/bg.gif)",backgroundSize:"100%"}}
		className="begin_box" >
		  
		  <p className="begin_a"></p>
		  <p className="begin_b"><span className="begin_c">50</span></p>
		  <a className="begin_d" onClick={this.btn}></a>
		</div>
		<div 
			style={{display:"none"}}
		className="anser" onClick={() => {
			if(localStorage.Login){
				if(this.state.info==""){
					hashHistory.push("information");

				}else if(this.state.info==""||this.state.info==""||this.state.info==""||this.state.info==""){
					
					hashHistory.push("pmh");
					sessionStorage.card="";
				}
				
			}else{
				hashHistory.push("login");
			}
			
		}}>
		  <div className="anser_a" >
			  <img src="images/homeimages/qb.gif" />
			</div>
			<div className="anser_b"></div>
			<div className="anser_c">
			<img src="images/homeimages/jt.gif" />
			</div>
		</div>
		<div className="fa"><div className="fa_a"></div><div></div></div>
		
		</div>
		)
	}
})


