import Top from "../../components/public/title";
import Footer from "../../components/public/footer_3.2";
import List from "../../components/prefect/list";
import "./style.less";
import url from "../../config/config";
import {hashHistory} from "react-router";
import {Toast} from "antd-mobile";
export default React.createClass({
    getInitialState(){
        return {
            userInfo:"",
            showwrite:false
        }
    },
    componentWillMount(){
        var that=this;
			var data=new FormData();
			data.append("userId",localStorage.userId);
	  
			fetch(url.url+"/api/act/mine/userInfo/getMyMessage.htm",{//
			  headers:{
				  token:localStorage.Token
			  },
			  method:"POST",body:data})
			  .then(r=>r.json())
			  .then((data)=>{
                  switch(data.code){
                    case 408:    Toast.info('', 1);
                                    break;
                    case 411:    Toast.info('', 1);
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    hashHistory.push("login");
                                    break;
                    case 410:    Toast.info('', 1);
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    hashHistory.push("login");
                                    break;
                    case 500:    Toast.info('', 1);
                                    break;
                    case 110001:    Toast.info('', 1);
                                    break;
                    default:        break;
                }    
                  that.setState(data.data);
                  if(data.data.userInfo==""||data.data.userInfo==""||data.data.userInfo==""){
                      that.setState({
                          showwrite:true
                      })
                  }
			  })
    },
    render:function(){
        return (
            <div className="perfect">
                <Top title="" back={true} write={
                    this.state.showwrite
                    }/>
                <List info={(this.state.userInfo==""||this.state.userInfo=="")?false:true}/>
                <Footer />
            </div>
        )
    }
})


