import {hashHistory,browserHistory} from "react-router";
export default React.createClass({
    render(){
        return (
            <div className="hello">
                <p onClick={() => {hashHistory.push("gvrp")}}></p>
                <p onClick={() => {hashHistory.push("getcoupon")}}></p>
                <p onClick={() => {hashHistory.push("waitcoupon")}}></p>
                <p onClick={() => {hashHistory.push("believe")}}></p>
                <p onClick={() => {hashHistory.push("tcsuccess")}}></p>
                <p onClick={() => {hashHistory.push("txing")}}></p>
                <p onClick={() => {hashHistory.push("overrepay")}}></p>
                <p onClick={() => {hashHistory.push("bzhome")}}></p>
            </div>
        )
    }
})


