import {Link,hashHistory,browserHistory} from "react-router";
import { Modal, Button, WhiteSpace, WingBlank, Toast } from 'antd-mobile';
import url from "../../config/config";
import store from "../../../store/store";
import fetch1 from "fetch-polyfill2";
const Out=React.createClass({
    getInitialState(){
        return {
            liked: true
        }
    },
    componentWillMount(){
        if(!sessionStorage.show){
            this.setState({
              liked:false
            })
        }
    },
    componentDidMount(){
        // alert(1)
    },
    render(){
        return (
            <div className="dropout">
                <div className="dropout_left"></div>
                <div className="dropout_right">
                    <span ></span>
                </div>
            </div>
        )
    }
})


const List=React.createClass({
    getInitialState(){
        return {

        }
    },
    componentWillMount(){
        // alert(1)
        console.log(store.getState())
        
    },
    render(){
        return (
            <div className="operationlist">
                <Link to={this.props.info.path}>
                    <div className="op_left">
                      <img src={this.props.info.img} />
                    </div>
                    <div className="op_right">
                        <span>{this.props.info.title}</span>

                    </div>
                 <div className="op_d"><img src="images/images/daikuan_08.gif" /></div>
                </Link>
            </div>
        )
    }
})





export default React.createClass({
    getInitialState(){
        return {liked: true};
    },
    btn(){
        
        localStorage.clear();
        sessionStorage.clear();
        hashHistory.push("home");
    },
    handleClickk: function(event) {
        this.setState({liked: !this.state.liked});
      },
    componentWillMount(){
            var that=this;
			var data=new FormData();
			data.append("userId",localStorage.userId);
	  
			fetch1(url.url+"/api/act/mine/userInfo/getMyMessage.htm",{
			  headers:{
				  token:localStorage.Token
			  },
			  method:"POST",body:data})
			  .then(r=>r.json())
			  .then((data)=>{
                  console.log(data)
                  switch(data.code){
                    case 413:    Toast.info(',', 1);
                                hashHistory.push("home");
                                break;
                    case 408:    Toast.info('', 1);
                                    break;
                    case 411:    Toast.info('', 1);
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    hashHistory.push("login");
                                    break;
                    case 410:    Toast.info('', 1);
                                    localStorage.clear();
                                    sessionStorage.clear();
                                    hashHistory.push("login");
                                    break;
                    case 500:    Toast.info('', 1);
                                    break;
                    case 160001:    Toast.info('', 1);
                                    break;
                    case 160002:    Toast.info('', 1);
                                    break;
                    default:        break;
                    }      
				  that.setState(data.data);
		
			  }).catch(function(e) {
                console.log("Oops, error");
                // Toast.info("", 2);
        });
    },
    btn1(){
        // console.log(1);
        if(this.state.userInfo==""){
            hashHistory.push("information")
        }else if(this.state.userInfo==""||this.state.userInfo==""||this.state.userInfo==""||this.state.userInfo==""){
            hashHistory.push("perfect")
        }
    },
    render(){
        var texts = this.state.liked ? 'none' : 'block';
        const info=this.props.info.map((con,index)=>{
            return <List key={index} info={con} />
        })
        return (
            <div className="infolist">
               <div className="infobg" style={{background:"url(images/images/my_02.gif)",backgroundSize:"100%"}}>
                <div className="amount_">
                 <p className="amount_y">{this.state.unuse}</p>
                 <p className="amount_x"></p>
                </div>
                <div className="amoun_t">
                  <div className="z_amount">
                    <p></p>
                    <p className="x_amount">{this.state.total}</p>
                  </div>
                  <div className="y_amount">
                    <p></p>
                    <p className="x2_amount">{this.state.used}</p>
                  </div>
                </div>
                </div>
                <div className="info_box">

            <div className="operationlist">
                <Link 
                onClick={this.btn1}
                >
                    <div className="op_left">
                      <img src="images/images/my_03 (1).gif" />
                    </div>
                    <div className="op_right" 
                        style={{position:"relative"}}
                    >
                        <span></span>
                        <span
                        style={{position:"absolute",width:"1.5rem",fontSize:"0.26rem",color:"#f99b47",left:"3rem"}}
                        >{this.state.userInfo==""||this.state.userInfo==""?"":""}</span>
                    </div>
                    
                 <div className="op_d"><img src="images/images/daikuan_08.gif" /></div>
                </Link>
            </div>
            <div className="operationlist">
                <Link to="loan">
                    <div className="op_left">
                      <img src="images/images/my_03 (2).gif" />
                    </div>
                    <div className="op_right">
                        <span></span>

                    </div>
                 <div className="op_d"><img src="images/images/daikuan_08.gif" /></div>
                </Link>
            </div>
            <div className="operationlist">
                <Link to="coupon">
                    <div className="op_left">
                      <img src="images/images/my_03 (3).gif" />
                    </div>
                    <div className="op_right">
                        <span></span>

                    </div>
                 <div className="op_d"><img src="images/images/daikuan_08.gif" /></div>
                </Link>
            </div>
                <div className="operationlist">
                <Link to="about">
                    <div className="op_left">
                      <img src="images/images/my_03 (4).gif" />
                    </div>
                    <div className="op_right">
                        <span></span>

                    </div>
                 <div className="op_d"><img src="images/images/daikuan_08.gif" /></div>
                </Link>
            </div>
            <div className="operationlist">
                <Link to="qr">
                    <div className="op_left">
                      <img src="images/images/my_03 (3).gif" />
                    </div>
                    <div className="op_right"
                        style={{position:"relative"}}
                    >
                        <span></span>
                        <span
                        style={{fontSize:"0.26rem",color:"#f99b47",position:"absolute",left:"3rem"}}
                    >{this.state.invitationCode}</span>
                    </div>
                    
                 <div className="op_d"><img src="images/images/daikuan_08.gif" /></div>
                </Link>
            </div>
            {/* <div className="operationlist">
                <Link onClick={this.btn}>
                    <div className="op_left">
                      <img src="images/images/my_03 (5).gif" />
                    </div>
                    <div className="op_right">
                        <span></span>

                    </div>
                 <div className="op_d"><img src="images/images/daikuan_08.gif" /></div>
                </Link>
            </div> */}
                  
                </div>
                <Link onClick={this.handleClickk} >
                    <div style={{width:"90%",height:"1rem",background:"#f99b47",margin:"auto",marginTop:"0.4rem",
                borderRadius:"0.1rem",textAlign:"center",lineHeight:"1rem",color:"#fff",fontSize:"0.32rem",cursor:"pointer"
                 }}>
                          
                    </div>   
                </Link>
                <div className="tx_tc" style={{display:texts}}>
                     <div className="tx_tc1">
                        <div className="tx_tca">
                            <div className="tx_tcaa"></div>
                        </div>
                        <div className="tx_tcb">
                            <Link className="tx_tcc" onClick={this.btn}></Link>
                            <div onClick={this.handleClickk}  className="tx_tcd"></div>
                        </div>
                     </div>
                </div>
            </div>
        )
    }
})


