import {InputItem,ImagePicker,Toast,Picker,List,Modal} from "antd-mobile";
import url from "../../config/config";
// import store from "../../../store/store";
import {hashHistory,browserHistory,Link} from "react-router";
import {compress} from "../../../utils/imgCompress";
import { setTimeout } from "timers";
export default React.createClass({
    getInitialState(){
        return {
            files:[],
            imgurl:"images/images/icon_11.jpg",
            imgup:"",
            certificateNo:"",
            companyName:[],
            title:"",
            list:[],
            check:true,
            info:false,
            value:[],//
            value2:[],//
            modal1:false,
            showsearch:false,//
            searchcon:"",//
        }
    },
    getinsurance(){//
        var that=this;
        var data=new FormData();
        data.append("companyName",this.state.searchcon);  
        fetch(url.url+"/api/act/mine/userInfo/insuranceList.htm",{
          headers:{
              token:localStorage.Token
          },
          method:"POST",body:data})
          .then(r=>r.json())
          .then((data)=>{
              console.log(data);




                var newlist=data.data.map((con)=>{
                        return {label:con.companyName,value:con.companyName}
                    })
               
                that.setState({
                    companyName:newlist,                
                }) 
                if(data.data.length==0){
                    that.setState({
                        pick:false,
                        companyName:[{label:'',value:''}],            
                    })
            }
          })
    },
    componentWillUnmount(){
        sessionStorage.stee=JSON.stringify(this.state);
    },
    componentWillMount(){
        if(sessionStorage.stee){
            this.setState(JSON.parse(sessionStorage.stee))
        }
        this.setState({modal1:false});//
           var that=this;//
			var data=new FormData();
			data.append("userId",localStorage.userId);
	  
			fetch(url.url+"/api/act/mine/userInfo/getMyMessage.htm",{
			  headers:{
				  token:localStorage.Token
			  },
			  method:"POST",body:data})
			  .then(r=>r.json())
			  .then((data)=>{			
                  if(data.data.userInfo==""){                       
                  }else{
                    that.setState({
                        info:true
                    })
                  }
               
			  }).catch(function(e) {
                console.log("Oops, error");
                // Toast.info("", 2);
        });
    },
    componentWillReceiveProps(nextprops){
        // console.log(nextprops);
        if(!sessionStorage.stee){
            this.setState({
                certificateNo:this.state.certificateNo?this.state.certificateNo:nextprops.data3.certificateNo,
                value:this.state.value[0]?this.state.value:[nextprops.data3.companyName],
                value2:this.state.value2[0]?this.state.value2:[nextprops.data3.title],
                imgurl:this.state.imgup?this.state.imgup:nextprops.data3.titleImg?nextprops.data3.titleImg:"images/images/icon_11.jpg",
                imgup:this.state.imgup?this.state.imgup:nextprops.data3.titleImg,
            })
        }
        
    },
    btn(){
        console.log(this.state)
        var that=this
         
        var reg=/^[0-9]{26}$/ig;
        if(!reg.test(this.state.certificateNo)){
            Toast.info("", 2);
        }
        else if(!this.state.value[0]){
            Toast.info("", 2);
        }
        else if(!this.state.value2[0]){
            Toast.info("", 2);
        }
        else if(!this.state.imgup){
            Toast.info("", 2);
        }else if(!this.state.check){
            Toast.info("", 2);
        }         
        else{
        var data=new FormData();
        data.append("certificateNo",this.state.certificateNo);
        data.append("companyName",this.state.value[0]);
        data.append("title",this.state.value2[0]);
        data.append("titleImg",this.state.imgup);       
        data.append("userId",localStorage.userId);
        fetch(url.url+"/api/act/mine/userInfo/saveWorkInfo.htm",{
        headers:{
            token:localStorage.Token
        },
        method:"POST",body:data})
        .then(r=>r.json())
        .then((data)=>{
            console.log(data);
            if(data.code=="200"){
                localStorage.couponinfo=JSON.stringify(data.data);
                console.log(localStorage.couponinfo)
                Toast.info(data.msg, 2);              
                if(that.state.info){//
                    that.props.changepage();
                    setTimeout(function(){
                        hashHistory.push("my");
                    },50) 
                    
                                      
                }else{
                    switch(that.state.value2[0]){
                        case "":
                        sessionStorage.creditmoney=20;
                        break;
                        case "":
                        sessionStorage.creditmoney=30;
                        break;
                        case "":
                        sessionStorage.creditmoney=40;
                        break;
                        case "":
                        sessionStorage.creditmoney=50;
                        break;
                    }
                    setTimeout(function(){                       
                        hashHistory.push("waitcoupon");
                    },200) 
                    
                   
                }                
            }else if(data.code=="400"){
                Toast.info(data.msg, 2);

            }else if(data.code=="410"){
                Toast.info("", 2); 
            }else if(data.code=="411"){
                Toast.info(",", 2); 
            }
        }).catch(function(e) {
                console.log("Oops, error");
                // Toast.info("", 2);
        });
    }
    },
    onChange(files, type, index){
        var that=this;
        if(!this.judgeImgType(files[0].file.type)){
             Toast.info("jpgjpegpng", 2);
             return
        }
        var data=new FormData();
        that.setState({modal1:true},()=>{
            data.append("img",files[0].url);     
            fetch(url.url+"/api/act/mine/userInfo/saveImg.htm",{
            headers:{
                token:localStorage.Token
            },
            method:"POST",body:data})
            .then(r=>r.json())
            .then((data)=>{
                console.log(data);
                that.setState({modal1:false});
                if(!data.data){
                        
                    if(data.code==400){
                        // Toast.info("jpgjpegpng", 2);
                    }else{
                        Toast.info("", 2);
                    }
                }else{
                    that.setState({
                        imgurl:data.data,
                        imgup:data.data
                        });
                }
               
                // that.setState({modal1:false});
            }).catch(function(e) {
                console.log("Oops, error");
                Toast.info("", 2);
                that.setState({modal1:false});  
            });           
        });
      },
    judgeImgType(file){
        var array = file.split('/');
        var index = array.length-1;
        if(array[index]=='jpg'||array[index]=='png'||array[index]=='jpeg'||array[index]=='JPG'||array[index]=='JPEG'||array[index]=='PNG'){
            return true;
        }else{
            return false;
        }
    },
    onClose(){
        this.setState({
        modal1: false,
        });
    },
    render(){
        const {files}=this.state;
        var showbox=this.props.page==4?"":"none";
        return (
            <div className="step_3" style={{display:showbox}}>
                <Modal
                    visible={this.state.modal1}
                    transparent
                    maskClosable={false}
                    onClose={this.onClose}
                    title=""
                    className="imgInfo"
                    >
                    <div>                        
                        <img src="images/images/loading.gif" alt=""/>
                        <p>...</p>
                    </div>
                </Modal>
                <div id="search" 
                    style={{display:this.state.showsearch?"":"none"}}//
                >
                    <div
                        className="con"
                        style={{paddingLeft:"0"}}
                    >
                        <div className="small"
                            onClick={()=>{
                                this.setState({
                                    showsearch:false
                                })
                            }}
                            style={{background:"url(images/images/cha1.png) 0% 0%/100%"}}
                        >
                        </div>
                        <div className="title1">
                            
                        </div>
                        <div className="serbox">
                                <input type="text" value={this.state.searchcon}
                                    onChange={(e)=>{
                                        this.setState({
                                            searchcon:e.target.value
                                        })
                                    }}
                                />
                                <div
                                    style={{background:"url(images/images/168859449437151898.png) 0% 0% / 100%"}}
                                    onClick={this.getinsurance}
                                >
                                    <Picker extra=""
                                    data={this.state.companyName}
                                    cols="1" 
                                    onOk={e => {
                                        if(e!=''){
                                            
                                            this.setState({value:e,showsearch:false});
                                        }else{
                                            this.setState({value:'',showsearch:false});
                                        }
                                    }
                                }
                                                onDismiss={e => console.log('dismiss', e)}
                                                >
                                                <List.Item
                                                    style={{width:"4rem"}}
                                                ></List.Item>
                                                </Picker> 
                                </div>  
                        </div>                     
                    </div>
                </div>                           
                <div className="title">
                    <img src="images/images/title_3.jpg" />
                </div>
                <div className="con">
                    <div className="tip">
                    <img src="images/images/icon_05.png" />
                        
                    </div>
                    <div className="wrap">
                        <div className="price">
                            <div className="top">
                                <span></span><InputItem
                                
                                value={this.state.certificateNo}
                                onChange={(e)=>{this.setState({
                                    certificateNo:e})}} 
                                style={{height:"0.52rem",fontSize:"0.28rem",flex:"1"}}
                                placeholder="" />
                            </div>
                            <div className="top">
                                <span></span>
                                <div
                                    style={{fontSize:"0.28rem",flex:"1"}}
                                    onClick={()=>{
                                        this.setState({
                                            showsearch:true
                                        })
                                    }}
                                >{this.state.value[0]?this.state.value[0]:""}</div>
                            </div>
                            <div className="top"
                                style={{borderTop:"1px solid #f89c47"}}
                            >
                                <span></span>
                                <Picker extra=""
                                    
                                    data={[{label:"",value:""},{label:"",value:""},{label:"",value:""},{label:"",value:""},]}
                                    cols="1" 
                                    value={this.state.value2}                                  
                                    onOk={e => {this.setState({value2:e})}}
                                    onDismiss={e => console.log('dismiss', e)}
                                    >
                                    <List.Item
                                        style={{width:"4rem"}}
                                    ></List.Item>
                                    </Picker>
                               
                            </div>
                            <div className="upimg">
                                
                            </div>
                            <div className="img_box">
                                <div className="left">
                                    <div
                                    style={{position:"relative",overflow:"hidden"}}
                                    >
                                        <img src={this.state.imgurl} />
                                        <ImagePicker
                                        style={{position:"absolute",top:"0",left:"0",width:"16rem",height:"15rem",opacity:"0"}}
                                        files={files}
                                        onChange={this.onChange}
                                        onImageClick={(index, fs) => console.log(index, fs)}
                                        selectable={files.length <1}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="sub">
                        <input type="checkbox" defaultChecked={this.state.check} onChange={()=>{this.setState({check:!this.state.check})}}/>
                        <p></p>
                        <Link to="auth"></Link>
                    </div>
                    <div className="step">
                        <button onClick={()=>{this.props.step(3)}}></button><button onClick={this.btn}></button>
                    </div>  
                    
                </div>
            </div>
            
        )
    }
})



