import {InputItem,ImagePicker,Toast,Picker,List,Modal} from "antd-mobile";
import url from "../../config/config";
import {compress} from "../../../utils/imgCompress";
import bank from "../method/bank";

export default React.createClass({
    getInitialState(){
        return {
            files:[],
            creditcard:"",
            imgurl3:"images/images/icon_09.jpg",
            imgurl4:"images/images/icon_10.jpg",
            imgup3:"",
            imgup4:"",
            // value:[""],//
            value2:[""],//
            banklist:[],
        }
    },
    
    componentWillUnmount(){
        sessionStorage.ste1=JSON.stringify(this.state);
        
    },
    componentWillReceiveProps(nextprops){
        if(!sessionStorage.ste1){
            this.setState({
                imgurl3:this.state.imgup3?this.state.imgup3:nextprops.data2.creditFrontImg?nextprops.data2.creditFrontImg:"images/images/icon_09.jpg",
                imgurl4:this.state.imgup4?this.state.imgup4:nextprops.data2.creditBackImg?nextprops.data2.creditBackImg:"images/images/icon_10.jpg",
                imgup3:this.state.imgup3?this.state.imgup3:nextprops.data2.creditFrontImg,
                imgup4:this.state.imgup4?this.state.imgup4:nextprops.data2.creditBackImg,
                value2:this.state.value2[0]?this.state.value2:[nextprops.data2.creditBank],
                creditcard:this.state.creditcard?this.state.creditcard:nextprops.data2.creditCardNo,
            })
        }

    },
    componentWillMount(){
        if(sessionStorage.ste1){
            this.setState(JSON.parse(sessionStorage.ste1))
        } 
        this.setState({modal1:false});//      
        var that=this;
         fetch(url.url+"/api/act/mine/bank/list.htm",{
            headers:{
                token:localStorage.Token
            },
            method:"get"})
            .then(r=>r.json())
            .then((data)=>{
                if(data.code=="410"){
                    Toast.info("", 2);
                    setTimeout(function(){
                        hashHistory.push("login")
                    },1000) 
                }else if(data.code=="411"){
                    Toast.info(",", 2);
                    setTimeout(function(){
                        hashHistory.push("login")
                    },1000) 
                }else if(data.code=="408"){
                    Toast.info("", 2);
                  }else if(data.code=="500"){
                    Toast.info("", 2);
                  }else{
                    var newlist=data.data.map((con)=>{
                        return {
                            label:con.bankName,
                            value:con.bankName
                        }
                    })
                    that.setState({
                        banklist:newlist,
                        creditcardlist:data.data
                    })
                  }
                
                
            }).catch(function(e) {
                console.log("Oops, error");
                Toast.info("", 2);
        });
            
       
    },
    btn(){
        console.log(this.state)
        if(!this.state.value2[0]){
            Toast.info("", 2);
        }else if(!this.state.creditcard){
            Toast.info("", 2);
            
        }else if(this.state.creditcard==this.state.bankcard){
            Toast.info("", 2);
        }
        else if(!this.state.imgup3){
            Toast.info("", 2);            
        }else if(!this.state.imgup4){
            Toast.info("", 2);
            
        }else{        
        console.log(this.state);
        console.log(localStorage.userId)
        var that=this;//
        var data=new FormData();
        data.append("creditBank",this.state.value2[0]);
        data.append("creditNo",this.state.creditcard);
        data.append("creditbackImg",this.state.imgup3);
        data.append("creditfrontImg",this.state.imgup4);
        data.append("userId",localStorage.userId);
        fetch(url.url+"/api/act/mine/bank/saveCredit.htm",{
        headers:{
            token:localStorage.Token
        },
        method:"POST",body:data})
        .then(r=>r.json())
        .then((data)=>{
            console.log(data);
            if(data.code=="200"){
                that.props.step(4)
            }else if(data.code=="400"){
                Toast.info(data.msg, 2);
            }
        }).catch(function(e) {
                console.log("Oops, error");
                Toast.info("", 2);
        });
    }
    },
    upimg(files){
        var that=this;
        return new Promise(function(suc,err){
        var data=new FormData();
        that.setState({modal1:true},()=>{
            data.append("img",files[0].url);   
            fetch(url.url+"/api/act/mine/userInfo/saveImg.htm",{
                headers:{
                    token:localStorage.Token
                },
                method:"POST",body:data})
                .then(r=>r.json())
                .then((data)=>{
                    that.setState({modal1:false});
                    if(!data.data){
                        
                        if(data.code==400){
                            // Toast.info("jpgjpegpng", 2);
                        }else{
                            Toast.info("", 2);
                        }
                    }else{
                        suc(data)
                    }
                    
                    
                }).catch(function(e) {
                console.log("Oops, error");
                Toast.info("", 2);
                that.setState({modal1:false});  
            });
        });
    })     
           
    },
    onChange3(files, type, index){
        var that=this;
        if(!this.judgeImgType(files[0].file.type)){
             Toast.info("jpgjpegpng", 2);
             return
        }
        this.upimg(files).then((data)=>{
            that.setState({
                imgurl3:data.data,
                imgup3:data.data
            })
        })
      },
    onChange4(files, type, index){
        var that=this;
        if(!this.judgeImgType(files[0].file.type)){
             Toast.info("jpgjpegpng", 2);
             return
        }
        this.upimg(files).then((data)=>{
            that.setState({
                imgurl4:data.data,
                imgup4:data.data
            })
        })
    },
    judgeImgType(file){
        var array = file.split('/');
        var index = array.length-1;
        if(array[index]=='jpg'||array[index]=='png'||array[index]=='jpeg'||array[index]=='JPG'||array[index]=='JPEG'||array[index]=='PNG'){
            return true;
        }else{
            return false;
        }
    },
    onClose(){
        this.setState({
        modal1: false,
        });
    },
    render(){
        const {files}=this.state;
        var showbox=this.props.page==3?"":"none";
        return (
            <div className="step_2" style={{display:showbox}}>
                 <Modal
                    visible={this.state.modal1}
                    transparent
                    maskClosable={false}
                    onClose={this.onClose}
                    title=""
                    className="imgInfo"
                    >
                    <div>                        
                        <img src="images/images/loading.gif" alt=""/>
                        <p>...</p>
                    </div>
                </Modal>
                <div className="title">
                    <img src="images/images/title.jpg" />
                </div>
                <div className="con">
                    <div className="tip">
                    <img src="images/images/icon_05.png" />
                        
                    </div>
                    <div className="wrap">
                        <div className="price">
                            <div className="top">
                                <span></span>
                                <Picker extra=""
                                    
                                    data={this.state.banklist}
                                    cols="1" 
                                    value={this.state.value2}                                  
                                    onOk={e => {this.setState({value2:e})}}
                                    onDismiss={e => console.log('dismiss', e)}
                                    >
                                    <List.Item
                                        style={{width:"4rem"}}
                                    ></List.Item>
                                    </Picker>
    
                            </div>
                            <div className="top">
                                <span></span><InputItem
                                value={this.state.creditcard}
                                onChange={(e)=>{
                                    this.setState({
                                        creditcard:e
                                    })
                                }} 
                                style={{height:"0.52rem",fontSize:"0.28rem",width:"4rem"}}
                                placeholder="" />
                            </div>
                            <div className="upimg">
                                
                            </div>
                            <div className="img_box">
                                <div className="left">
                                    <div 
                                        style={{position:"relative",overflow:"hidden"}}
                                    >
                                        <img src={this.state.imgurl3} />
                                        <ImagePicker
                                        style={{position:"absolute",top:"0",left:"0",width:"16rem",height:"15rem",opacity:"0"}}
                                        files={files}
                                        onChange={this.onChange3}
                                        onImageClick={(index, fs) => console.log(index, fs)}
                                        selectable={files.length <1}
                                        />
                                    </div>
                                    
                                    <p></p>
                                </div>
                                <div className="right">
                                    <div
                                        style={{position:"relative",overflow:"hidden"}}
                                    >
                                        <img src={this.state.imgurl4} />
                                        <ImagePicker
                                        style={{position:"absolute",top:"0",left:"0",width:"16rem",height:"15rem",opacity:"0"}}
                                        files={files}
                                        onChange={this.onChange4}
                                        onImageClick={(index, fs) => console.log(index, fs)}
                                        selectable={files.length <1}
                                        />
                                    </div>
                                    
                                    <p></p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="step">
                        <button onClick={                                                      
                            ()=>{this.props.step(2)}}></button> <button onClick={                                
                                this.btn}></button>
                    </div>  
                </div>
            </div>
        )
    }
})



