import {InputItem,ImagePicker,Toast,Picker,List,Modal} from "antd-mobile";
import url from "../../config/config";
import {compress} from "../../../utils/imgCompress";
import bank from "../method/bank";
import fetch1 from "fetch-polyfill2";
export default React.createClass({
    getInitialState(){
        return {
            files:[],
            bankcard:"",
            imgurl:"images/images/icon_09.jpg",
            imgurl2:"images/images/icon_10.jpg",
            imgup1:"",
            imgup2:"",
            value:[""],//
            pwd:"",
            tip:"",
            oldcard:"",
            showpwd:false,//
            dbclick:true,//
        }
    },
    
    componentWillUnmount(){
        sessionStorage.ste=JSON.stringify(this.state);
        clearInterval(this.timer)
        
    },
    componentWillReceiveProps(nextprops){
        console.log(nextprops)
        if(!sessionStorage.ste){
            this.setState({
                value:this.state.value[0]?this.state.value:[nextprops.data2.bank],
                bankcard:this.state.bankcard?this.state.bankcard:nextprops.data2.cardNo,
                imgurl:this.state.imgup1?this.state.imgup1:nextprops.data2.frontImg?nextprops.data2.frontImg:"images/images/icon_09.jpg",
                imgurl2:this.state.imgup2?this.state.imgup2:nextprops.data2.backImg?nextprops.data2.backImg:"images/images/icon_10.jpg",
                imgup1:this.state.imgup1?this.state.imgup1:nextprops.data2.frontImg,
                imgup2:this.state.imgup2?this.state.imgup2:nextprops.data2.backImg,
                oldcard:this.state.oldcard?this.state.oldcard:nextprops.data2.cardNo
            })
        }

    },
    componentWillMount(){
        if(sessionStorage.ste){
            this.setState(JSON.parse(sessionStorage.ste))
        } 
        this.setState({modal1:false,tip:"",dbclick:true});//      
        var that=this;
         fetch(url.url+"/api/act/mine/bank/list.htm",{
            headers:{
                token:localStorage.Token
            },
            method:"get"})
            .then(r=>r.json())
            .then((data)=>{
                if(data.code=="410"){
                    Toast.info("", 2);
                    setTimeout(function(){
                        hashHistory.push("login")
                    },2000) 
                }else if(data.code=="411"){
                    Toast.info(",", 2);
                    setTimeout(function(){
                        hashHistory.push("login")
                    },2000) 
                }else if(data.code=="408"){
                    Toast.info("", 2);
                  }else if(data.code=="500"){
                    Toast.info("", 2);
                  }else{
                    var newlist=data.data.map((con)=>{
                        return {
                            label:con.bankName,
                            value:con.bankName
                        }
                    })
                    that.setState({
                        banklist:newlist,
                        creditcardlist:data.data
                    })
                  }
                
                
            }).catch(function(e) {
                console.log("Oops, error");
                Toast.info("", 2);
        });
            
       
    },
    btn(){
        console.log(this.state)
        if(!this.state.value[0]){
            Toast.info("", 2);
        }else if(!this.state.bankcard){
            Toast.info("", 2);
            
        }else if(!bank(this.state.bankcard)){
            Toast.info("", 2);
        }
        else if(this.state.oldcard!=this.state.bankcard&&!this.state.pwd){           
            Toast.info("", 2);          
        }
        else if(!this.state.imgup1){
            Toast.info("", 2);
        }else if(!this.state.imgup2){
            Toast.info("", 2);
        }
        else{
        var that=this;//
        var data=new FormData();
        data.append("backImg",this.state.imgup2);
        data.append("bank",this.state.value[0]);
        data.append("cardNo",this.state.bankcard+","+this.state.pwd);
        data.append("frontImg",this.state.imgup1);
        data.append("userId",localStorage.userId);
        // data.append("validatecode",this.state.pwd);


        fetch(url.url+"/api/act/mine/bank/saveBank.htm",{
        headers:{
            token:localStorage.Token
        },
        method:"POST",body:data})
        .then(r=>r.json())
        .then((data)=>{
            console.log(data);
            if(data.code=="200"){
                that.setState({
                    oldcard:that.state.bankcard,
                    showpwd:false
                })
                that.props.step(3)
            }else if(data.code=="400"){
                Toast.info(data.msg, 2);
            }
        }).catch(function(e) {
                console.log("Oops, error");
                Toast.info("", 2);
        });
    }
    },
    upimg(files){
        var that=this;
        return new Promise(function(suc,err){
        var data=new FormData();
        that.setState({modal1:true},()=>{
            data.append("img",files[0].url);   
            fetch(url.url+"/api/act/mine/userInfo/saveImg.htm",{
                headers:{
                    token:localStorage.Token
                },
                method:"POST",body:data})
                .then(r=>r.json())
                .then((data)=>{
                    that.setState({modal1:false});
                    if(!data.data){
                        
                        if(data.code==400){
                            // Toast.info("jpgjpegpng", 2);
                        }else{
                            Toast.info("", 2);
                        }
                    }else{
                        suc(data)
                    }
                    
                    
                }).catch(function(e) {
                console.log("Oops, error");
                Toast.info("", 2);
                that.setState({modal1:false});  
            });
        });
    })     
           
    },
    onChange(files, type, index){
        
        var that=this;
        if(!this.judgeImgType(files[0].file.type)){
             Toast.info("jpgjpegpng", 2);
             return
        }
        this.upimg(files).then((data)=>{
            that.setState({
                // imgurl:files[0].url,
                imgurl:data.data,
                imgup1:data.data
            })
        })
      },
    onChange2(files, type, index){
        console.log(2)
        var that=this;
        if(!this.judgeImgType(files[0].file.type)){
             Toast.info("jpgjpegpng", 2);
             return
        }
        this.upimg(files).then((data)=>{
            that.setState({
                imgurl2:data.data,
                imgup2:data.data
            })
        })
      },
    send(){
        var that=this;
        if(this.state.dbclick){
        if(this.state.tip==""){
        if(!this.state.bankcard){
            Toast.info("", 2);           
        }else if(!bank(this.state.bankcard)){
            Toast.info("", 2);
        }else{   
                this.setState({
                    dbclick:false
                })           
                var data=new FormData();
                data.append("userId",localStorage.userId);
                data.append("cardNo",this.state.bankcard);
                data.append("phone",localStorage.Phone);

                fetch1(url.url+"/api/act/mine/bank/authBindCardReq.htm",{
                headers:{
                    token:localStorage.Token
                },
                method:"POST",body:data})
                .then(r=>r.json())
                .then((data)=>{
                    // console.log(data)
                    
                    if(data.code=="200"){
                        that.setState({
                            dbclick:false
                        })
                        Toast.info("", 2);
                        var i=120;
                        that.timer=setInterval(function(){
                            i--;
                            that.setState({
                                tip:i+""
                            })
                            if(i==0){
                                clearInterval(that.timer);
                                that.setState({
                                    tip:"",
                                    dbclick:true
                                })
                                
                            }
                        },1000)
                    }else{
                        Toast.info(data.msg, 2);
                        that.setState({
                            dbclick:true
                        })
                    }

                })
            
            }
    }            
}
    },
    judgeImgType(file){
        var array = file.split('/');
        var index = array.length-1;
        if(array[index]=='jpg'||array[index]=='png'||array[index]=='jpeg'||array[index]=='JPG'||array[index]=='JPEG'||array[index]=='PNG'){
            return true;
        }else{
            return false;
        }
    },
    onClose(){
        this.setState({
        modal1: false,
        });
    },
    render(){
        const {files}=this.state;
        var showbox=this.props.page==2?"":"none";
        return (
            <div className="step_2" style={{display:showbox}}>
                 <Modal
                    visible={this.state.modal1}
                    transparent
                    maskClosable={false}
                    onClose={this.onClose}
                    title=""
                    className="imgInfo"
                    >
                    <div>                        
                        <img src="images/images/loading.gif" alt=""/>
                        <p>...</p>
                    </div>
                </Modal>
                <div className="title">
                    <img src="images/images/title.jpg" />
                </div>
                <div className="con">
                    <div className="tip">
                    <img src="images/images/icon_05.png" />
                        
                    </div>
                    <div className="wrap">
                        <div className="price">
                            <div className="top">
                                <span></span>
                                <Picker extra=""
                                    
                                    data={this.state.banklist}
                                    cols="1" 
                                    value={this.state.value}                                  
                                    onOk={e => {this.setState({value:e})}}
                                    onDismiss={e => console.log('dismiss', e)}
                                    >
                                    <List.Item
                                        style={{width:"4rem"}}
                                    ></List.Item>
                                    </Picker>
                            </div>
                            <div className="top">
                                <span></span><InputItem
                                style={{color: "#888"}}
                                value={this.state.bankcard}
                                onChange={(e)=>{
                                    // console.log(e)
                                    if(e!=this.state.oldcard){
                                        this.setState({
                                            showpwd:true
                                        })
                                    }else{
                                        this.setState({
                                            showpwd:false
                                        })
                                    }
                                    this.setState({
                                        bankcard:e,
                                        // showpwd:true
                                    });       
                                }} 
                                style={{height:"0.52rem",fontSize:"0.28rem",width:"4rem"}}
                                placeholder="" />
                            </div>
                            <div className="top" style={{borderTop:"1px solid #f89c47",display:this.state.showpwd?"":"none"}}>
                                <span></span>
                                <input 
                                style={{color:"#555",height:"0.52rem",fontSize:"0.28rem",width:"2rem",marginLeft:"0.3rem",border:"0"}}
                                placeholder=""
                                value={this.state.pwd}
                                onChange={(e)=>{
                                    console.log(e.target.value);
                                    this.setState({
                                        pwd:e.target.value
                                    })
                                }} 
                                />
                                <span
                                    onClick={this.send}
                                    style={{color:this.state.tip==""?"#fa9b47":"#555",height:"0.72rem",lineHeight:"0.72rem",width:"2.3rem",textAlign:"center"}}
                                >{this.state.tip}</span>
                            </div>
                            <div className="upimg">
                                
                            </div>
                            <div className="img_box">
                                <div className="left">
                                    <div 
                                        style={{position:"relative",overflow:"hidden"}}
                                    >
                                        <img src={this.state.imgurl} />
                                        <ImagePicker
                                        style={{position:"absolute",top:"0",left:"0",width:"16rem",height:"15rem",opacity:"0"}}
                                        files={files}
                                        onChange={this.onChange}
                                        onImageClick={(index, fs) => console.log(index, fs)}
                                        selectable={files.length <1}
                                        />
                                    </div>
                                    
                                    <p></p>
                                </div>
                                <div className="right">
                                    <div
                                        style={{position:"relative",overflow:"hidden"}}
                                    >
                                        <img src={this.state.imgurl2} />
                                        <ImagePicker
                                        style={{position:"absolute",top:"0",left:"0",width:"16rem",height:"15rem",opacity:"0"}}
                                        files={files}
                                        onChange={this.onChange2}
                                        onImageClick={(index, fs) => console.log(index, fs)}
                                        selectable={files.length <1}
                                        />
                                    </div>
                                    
                                    <p></p>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                    <div className="step">
                        <button onClick={                                                      
                            ()=>{this.props.step(1)}}></button> <button onClick={
                                
                                this.btn
                                
                                }></button>
                    </div>  
                </div>
            </div>
        )
    }
})



