import url from "../../config/config";
import {Toast} from "antd-mobile";
import {hashHistory} from "react-router";
import store from "../../../store/store";
import {Modal} from "antd-mobile";
export default React.createClass({
    getInitialState(){
        return {
            ceateTime:"",
            repayTime:"",
            createTime:"",
            modal1:false,
            interest:"",
            repayAmount:"",
            cardNo:""
        }
    },
    btn(){
        // console.log(1)
        this.setState({
            modal1:true
        });
        var that=this;
        var data=new FormData();//
        data.append("orderId",this.state.orderId);
        data.append("userId",localStorage.userId);
        fetch(url.url+"/api/act/pay/repayment/repay.htm",{
            headers:{
                token:localStorage.Token
            },
            method:"POST",body:data})
            .then(r=>r.json())
            .then((data)=>{
              switch(data.code){
                case 400:    Toast.info('', 1);
                                  hashHistory.goBack()
                                  break;
                case "300":    setTimeout(function(){
                                Toast.info(data.msg, 1);
                                that.setState({
                                    modal1:false
                                })
                                hashHistory.goBack()                               
                                },5000);
                                break;
                                
                case "600":    Toast.info(',', 1);
                                  hashHistory.goBack()
                                  break;
                case 408:    Toast.info('', 1);
                                     hashHistory.goBack()
                                break;
                case 411:    Toast.info('', 1);
                                localStorage.clear();
                                sessionStorage.clear();
                                hashHistory.push("login");
                                break;
                case 410:    Toast.info('', 1);
                                localStorage.clear();
                                sessionStorage.clear();
                                hashHistory.push("login");
                                break;
                case "500":    Toast.info('', 1);
                                hashHistory.goBack()
                                break;
                case "200":    setTimeout(function(){
                                    Toast.info('', 1);
                                    that.setState({
                                        modal1:false
                                    })
                                    sessionStorage.pagenum=3;
                                    hashHistory.push("loan")
                                },5000)
                                
                                break;
                case 150005:    Toast.info('', 1);
                                hashHistory.goBack()
                                break;
                case 150006:    Toast.info('', 1);
                                 hashHistory.goBack()
                                break;
                default:        break;
                }                     
            })
    },
    componentWillMount(){
        var orderNo=this.props.orderNo;
        console.log(orderNo)
        var that=this;
        var data=new FormData();
        data.append("userId",localStorage.userId);
      
        data.append("orderNo",orderNo);

        fetch(url.url+"/api/act/mine/borrow/orderInfo.htm",{
            headers:{
                token:localStorage.Token
            },
            method:"POST",body:data})
            .then(r=>r.json())
            .then((data)=>{
            console.log(data)   
             switch(data.code){
                case 408:    Toast.info('', 1);
                                break;
                case 411:    Toast.info('', 1);
                                localStorage.clear();
                                sessionStorage.clear();
                                hashHistory.push("login");
                                break;
                case 410:    Toast.info('', 1);
                                localStorage.clear();
                                sessionStorage.clear();
                                hashHistory.push("login");
                                break;
                case 500:    Toast.info('', 1);
                                break;
                case 150001:    Toast.info('', 1);
                                break;
                case 150003:    Toast.info('', 1);
                                break;
                default:        break;
                }     
            that.setState(data.data[0])
                
            }).catch(function(e) {
                console.log("Oops, error");
                // Toast.info("", 2);
        });
    },
    render(){
        return (
            <div className="already-box">
            <Modal
                    visible={this.state.modal1}
                    transparent
                    maskClosable={false}
                    onClose={this.onClose}
                    title=""
                    className="imgInfo"
                    >
                    <div>                        
                        <img src="images/images/loading.gif" alt=""/>
                        <p>...</p>
                    </div>
            </Modal>
            <div className="already-tops" style={{background:"url(images/images/repay-bg.png)",backgroundSize:"100%"}}>
                {/* <div className="already-head"> 11</div> */}
                <div className="already-min1"
                    style={{display:this.state.remitAmount>0?"":"none"}}
                >{this.state.remitAmount}</div>
                <div className="already-min2"><span className="already-min2a">{this.state.repayAmount}</span><span className="already-min2b">{String(this.state.repayAmount).indexOf(".")!=-1?"0":".00"}</span></div>
                <div className="already-min3">()</div>
                <div className="already-list">
                    <div className="already-list1">
                        <div className="already-list1a">
                            <div className="already-list1a1">{this.state.amount}<span className="already-list1a1a">.00</span></div>
                            <div className="already-list1a2">()</div>
                        </div>
                        <div className="already-list1b">
                            <div className="already-list1b1"></div>
                        </div>
                        <div className="already-list1c">
                            <div className="already-list1a1">{this.state.realAmount}<span className="already-list1a1a">.00</span></div>
                            <div className="already-list1a2">()</div>
                        </div>
                    </div>
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.borrowType}</div>
                    </div>
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.serviceFee}.00</div>
                    </div>
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.interest}{String(this.state.interest).indexOf(".")!=-1?"0":".00"}</div>
                    </div>
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.extensionAmount}.00</div>
                    </div>
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.timeLimit}</div>
                    </div>
                    
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.createTime.split(" ")[0]}</div>
                    </div>
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.repayTime.split(" ")[0]}</div>
                    </div>
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.orderNo}</div>
                    </div>
                    <div className="already-list2">
                        <div className="already-list2a"></div>
                        <div className="already-list2b">{this.state.cardNo.substr(0,4)+"***********"+this.state.cardNo.substr(-4)}</div>
                    </div>
                    <div className="already-list3" onClick={this.btn}></div>
                </div>
            </div>
        </div>
        )
    }
})


