import {InputItem,ImagePicker,Toast,Modal } from "antd-mobile";
import url from "../../config/config";
import $ from "jquery";
// import EXIF from "exif-js";
import {compress} from "../../../utils/imgCompress";
import idcard from "../method/idcard";

export default React.createClass({
    getInitialState(){
        return {
            name:"",
            phone:localStorage.Phone,
            idcard:"",
            files:[],
            imgurl:"images/images/icon_08.jpg",
            imgurl2:"images/images/icon_06.jpg",
            imgurl3:"images/images/icon_07.jpg",
            upimg1:"",
            upimg2:"",
            upimg3:"",
            email:"",
        }
    },
    btn(){
      //
      var reg=/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/;
        if(!/^[\u4e00-\u9fa5]{2,4}$/g.test(this.state.name)){
            Toast.info("", 2);
        }else if(!this.state.email){
            Toast.info("", 2);
        }else if(!reg.test(this.state.email)){
            Toast.info("", 2);
        }
        else if(!this.state.idcard){
            Toast.info("", 2);
        }else if(!idcard(this.state.idcard)){
            Toast.info("", 2);
        }
        
        else if(!this.state.upimg1||!this.state.upimg2||!this.state.upimg3){
            Toast.info("", 2);
        }else{
            
        var that=this;
        var data=new FormData();
        data.append("backImg",this.state.upimg2);
        data.append("frontImg",this.state.upimg1);
        data.append("livingImg",this.state.upimg3);
        data.append("idNo",this.state.idcard);
        data.append("phone",this.state.phone);
        data.append("email",this.state.email);
        data.append("realName",this.state.name);
        data.append("userId",localStorage.userId);
        fetch(url.url+"/api/act/mine/userInfo/save.htm",{
        headers:{
            token:localStorage.Token
        },
        method:"POST",body:data})
        .then(r=>r.json())
        .then((data)=>{
            console.log(data)
            if(data.code=="200"){
                that.props.step(2)
            }else if(data.code=="400"){
                Toast.info(data.msg, 2);
            }
        }).catch(function(e) {
                Toast.info("", 2);
        });
    }
    },
    step(e){
        e.nativeEvent.preventDefault();
    },
    componentWillMount(){
        if(sessionStorage.step){
            this.setState(JSON.parse(sessionStorage.step))
        }
        this.setState({modal1:false});//
    },
    componentWillUnmount(){
        console.log("xiezai")
        sessionStorage.step=JSON.stringify(this.state);
    },
    componentWillReceiveProps(nextprops){
        if(!sessionStorage.step){
            this.setState({
                name:this.state.name?this.state.name:nextprops.data1.realName,
                email:this.state.email?this.state.email:nextprops.data1.email,
                idcard:this.state.idcard?this.state.idcard:nextprops.data1.idNo,
                imgurl:this.state.upimg1?this.state.upimg1:nextprops.data1.userFrontImg?nextprops.data1.userFrontImg:"images/images/icon_08.jpg",
                imgurl2:this.state.upimg2?this.state.upimg2:nextprops.data1.userBackImg?nextprops.data1.userBackImg:"images/images/icon_06.jpg",
                imgurl3:this.state.upimg3?this.state.upimg3:nextprops.data1.userLivingImg?nextprops.data1.userLivingImg:"images/images/icon_07.jpg",
                upimg1:this.state.upimg1?this.state.upimg1:nextprops.data1.userFrontImg,
                upimg2:this.state.upimg2?this.state.upimg2:nextprops.data1.userBackImg,
                upimg3:this.state.upimg3?this.state.upimg3:nextprops.data1.userLivingImg,
            })
        }else{
            // this.setState({
            //     imgurl:"images/images/icon_08.jpg",
            //     imgurl2:"images/images/icon_06.jpg",
            //     imgurl3:"images/images/icon_07.jpg",
            // })
        }
        
    },
    upimg(files){
        var that=this;
        return new Promise(function(suc,err){ 
        var data=new FormData();
        that.setState({modal1:true},()=>{
            data.append("img",files[0].url);  
            fetch(url.url+"/api/act/mine/userInfo/saveImg.htm",{
                headers:{
                    token:localStorage.Token
                },
                method:"POST",body:data})
                .then(r=>r.json())
                .then((data)=>{
                    that.setState({modal1:false});
                    if(!data.data){
                        
                        if(data.code==400){
                            // Toast.info("jpgjpegpng", 2);
                        }else{
                            Toast.info("", 2);
                        }
                    }else{
                        suc(data)
                    }                    
                    
                
            }).catch(function(e) {
                that.setState({modal1:false});  
                Toast.info("", 2);
            });
        });
        // return p;
    })
        
             
    },
    onChange(files, type, index){
        var that=this;
        if(!this.judgeImgType(files[0].file.type)){
             Toast.info("jpgjpegpng", 2);
             return
        }
        this.upimg(files).then((data)=>{
            // var img = new Image();
            // var imgurl = "imgurl";
            // img.src = files[0].url;
            // var newimg = that.handelImg(img,1);
            that.setState({
                // imgurl:files[0].url,
                imgurl:data.data,
                upimg1:data.data
            });
        })
                    
      },
    onChange2(files, type, index){
        var that=this;
        if(!this.judgeImgType(files[0].file.type)){
             Toast.info("jpgjpegpng", 2);
             return
        }
        this.upimg(files).then((data)=>{
            that.setState({
                // imgurl2:files[0].url,
                imgurl2:data.data,
                upimg2:data.data
            })
        })
      },
    onChange3(files, type, index){
        if(!this.judgeImgType(files[0].file.type)){
             Toast.info("jpgjpegpng", 2);
             return
        }
        var that=this;
        this.upimg(files).then((data)=>{
            that.setState({
                // imgurl3:files[0].url,
                imgurl3:data.data,
                upimg3:data.data
            })
        })
      },   
    judgeImgType(file){
        var array = file.split('/');
        var index = array.length-1;
        if(array[index]=='jpg'||array[index]=='png'||array[index]=='jpeg'||array[index]=='JPG'||array[index]=='JPEG'||array[index]=='PNG'){
            return true;
        }else{
            return false;
        }
    },
    handelImg(image,imgurl){
        var that = this;
        //
        var width = image.width;
        var height = image.height;
        var canvas = document.createElement("canvas")
        var ctx = canvas.getContext('2d');
        var newImage = new Image();
        EXIF.getData(image,function () {
            var orientation = EXIF.getTag(this,'Orientation');
            // orientation = 6;//
            console.log('orientation:'+orientation);
            switch (orientation){
                //
                case 1:
                    console.log('0');
                    alert('0');
                    // canvas.height = height;
                    // canvas.width = width;
                    newImage = image;
                    break;
                //90
                case 6:
                    console.log('90');
                    alert('90');
                    canvas.height = width;
                    canvas.width = height;
                    ctx.rotate(Math.PI/2);
                    ctx.translate(0,-height);
                    ctx.drawImage(image,0,0)
                    imageDate = canvas.toDataURL('Image/jpeg',1)
                    newImage.src = imageDate;
                    break;
                //180
                case 3:
                    console.log('180');
                    alert('180');
                    canvas.height = height;
                    canvas.width = width;
                    ctx.rotate(Math.PI);
                    ctx.translate(-width,-height);
                    ctx.drawImage(image,0,0)
                    imageDate = canvas.toDataURL('Image/jpeg',1);
                    newImage.src = imageDate;
                    break;
                //270
                case 8:
                    console.log('270');
                    alert('270');
                    canvas.height = width;
                    canvas.width = height;
                    ctx.rotate(-Math.PI/2);
                    ctx.translate(-height,0);

                    ctx.drawImage(image,0,0)
                    imageDate = canvas.toDataURL('Image/jpeg',1)
                    newImage.src = imageDate;
                    break;
                //undefined
                case undefined:
                    console.log('undefined  ');
                    alert('undefined  ');
                    newImage = image;
                    break;
                }
            }
        );
        // alert(newImage.src);
        switch(imgurl){            
            case 1: that.setState({
                        imgurl:newImage.src
                    });
                    break;
            case 2: that.setState({
                        imgurl2:newImage.src
                    });
                    break;
            case 3: that.setState({
                        imgurl3:newImage.src
                    });
                    break;
            default: break;
        }
        
        // return newImage;
    },
    onClose(){
        this.setState({
        modal1: false,
        });
    },
    render(){
        const {files}=this.state;
        var showbox=this.props.page==1?"":"none";
        return (
            <div className="step_1"  style={{display:showbox}}>
                <Modal
                    visible={this.state.modal1}
                    transparent
                    maskClosable={false}
                    onClose={this.onClose}
                    title=""
                    className="imgInfo"
                    >
                    <div>                        
                        <img src="images/images/loading.gif" alt=""/>
                        <p>...</p>
                    </div>
                </Modal>
                <div className="title">
                    <img src="images/images/title_2.jpg" />
                </div>
                    <div className="con">
                    <div className="tip">
                        {/* <i
                            style={{background:"url(images/images/icon_05.png)",backgroundSize:"100%"}}
                        ></i> */}
                        <img src="images/images/icon_05.png" />
                        
                    </div>    
                    <div className="price">
                        <div className="top">
                            <span></span><InputItem 
                            style={{height:"0.52rem",fontSize:"0.28rem"}}
                            value={this.state.name}
                            onChange={(e)=>{
                                this.setState({
                                    name:e
                                })
                            }}
                            placeholder="" />
                        </div>
                        <div className="top">
                            <span></span><InputItem
                            value={this.state.phone}
                            
                            style={{height:"0.52rem",fontSize:"0.28rem"}}
                            placeholder="" />
                        </div>
                        <div className="top">
                            <span></span><InputItem
                            value={this.state.email}
                            onChange={(e)=>{
                                this.setState({
                                    email:e
                                })
                            }}
                            style={{height:"0.52rem",fontSize:"0.28rem"}}
                            placeholder="" />
                        </div>
                    </div>
                    <div className="tip_2">
                    <img src="images/images/icon_05.png" />
                        
                    </div>
                    {/* <div>{this.state.upimg1}</div> */}
                    <div className="price">
                        <div className="top">
                            <span></span><InputItem
                            value={this.state.idcard}
                            onChange={(e)=>{
                                this.setState({
                                    idcard:e
                                })
                            }} 
                           
                            style={{height:"0.52rem",fontSize:"0.28rem"}}
                            placeholder="" />
                        </div>
                        <div className="top"
                            style={{borderBottom:"none"}}
                        >
                            <span
                                style={{width:"100%"}}
                            ></span>
                        </div>
                        <div className="uplist">
                            <div 
                                style={{position:"relative"}}
                            >
                                <img src={this.state.imgurl}/>
                                <p></p>
                                <ImagePicker
                                style={{position:"absolute",top:"0",left:"0",width:"6rem",height:"5rem",opacity:"0"}}
                                files={files}
                                onChange={this.onChange}
                                onImageClick={(index, fs) => console.log(index, fs)}
                                selectable={files.length <2}
                                />
                            </div>
                            <div
                                style={{position:"relative"}}
                            >
                                <img src={this.state.imgurl2}/>
                                <p></p>
                                <ImagePicker
                                style={{position:"absolute",top:"0",left:"0",width:"6rem",height:"5rem",opacity:"0"}}
                                files={files}
                                onChange={this.onChange2}
                                onImageClick={(index, fs) => console.log(index, fs)}
                                selectable={files.length <1}
                                />
                            </div>
                            <div
                                style={{position:"relative"}}
                            >
                                <img src={this.state.imgurl3}/>
                                <p></p>
                                <ImagePicker
                                style={{position:"absolute",top:"0",left:"0",width:"6rem",height:"5rem",opacity:"0"}}
                                files={files}
                                onChange={this.onChange3}
                                onImageClick={(index, fs) => console.log(index, fs)}
                                selectable={files.length <1}
                                />
                            </div>
                        </div>
                    </div>
                    <div className="next">
                        <input type="button" value="" onClick={
                            this.btn
                            } />
                    </div>
                </div>
                
            </div>
        )
    }
})



