import {InputItem,Toast,Picker,List} from "antd-mobile";
import {hashHistory} from "react-router";
import url from "../../config/config";
export default React.createClass({
    getInitialState(){
        return {
            num:"",
            ti1:"",
            ti2:"",
            ti3:1,
            ti4:"",
            ti5:1,
            ti6:1,
            ti7:1,
            ti8:["0-3000/"],
            showMortgage:true,//
            showExtension:false,//
        }
    },
    btn(e){
        //
        var mortgage=this.state.ti2;//
        var extension=this.state.ti3;//
        if(/^[1-9]{1}[0-9]{0,5}$/g.test(this.state.num)){
            if(this.state.ti1==""){
                extension=null;
            }else if(this.state.ti1==""){
                mortgage=""
            }else{
                mortgage="";
                extension=null;
            }
        var data=new FormData();
        data.append("accumulationFund",this.state.ti5);
        data.append("carMortgage",this.state.ti4);
        data.append("houseExtension",extension);
        data.append("houseMortgage",mortgage);
        data.append("houseOwnership",this.state.ti1);
        data.append("insuranceInfo",this.state.ti7);
        data.append("loanAmount",this.state.num);
        data.append("salary",this.state.ti8[0]);
        data.append("socialSecurity",this.state.ti6);
        data.append("userId",localStorage.userId);
        fetch(url.url+"/api/act/mine/userExtraInfo/saveWantMakeMoney.htm",{
            headers:{
                token:localStorage.Token
            },
            method:"POST",body:data})
            .then(r=>r.json())
            .then((data)=>{
                console.log(data)

                if(data.code=="250013"){
                    Toast.info(data.msg, 2);
                    hashHistory.push("home")
                }else{
                    Toast.info(data.msg, 2);
                }
            }).catch(function(e) {
                console.log("Oops, error");
                Toast.info("", 2);
        });
    }else{
        Toast.info("")
    }
    },
    render(){
        return (
            <div className="list_con">
                <div className="tips">
                <img src="images/images/icon_05.png" />
                    <span></span>
                </div>
                <div className="title">
                    <i
                        style={{background:"url(images/images/10475463301731984.png) 0% 0% no-repeat",backgroundSize:"contain"}}
                    ></i>
                    <span
                    ></span>
                    <InputItem
                    value={this.state.num}
                    onChange={(e)=>{this.setState({num:e})}}
                    style={{height:"0.42rem",fontSize:"0.28rem",marginLeft:"0.2rem"}} 
                    placeholder=""
                    />
                </div>
                <div className="tips">
                <img src="images/images/icon_05.png" />
                    <span></span>
                </div>
                <div className="list_info">
                    <div>
                    <span></span>
                    <span><label><div
                            style={{background:this.state.ti1==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        >
                        <input type="radio" name="ti1" defaultChecked={this.state.ti1==""?"true":""} onChange={(e)=>{
                            if(e.target.checked){
                                this.setState({
                                    showMortgage:true,
                                    showExtension:false
                                })
                            }
                            this.setState({
                                ti1:""
                            })
                        }}/>
                            </div></label></span>
                        <span
                        ><label><div
                        style={{background:this.state.ti1==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti1" defaultChecked={this.state.ti1==""?"true":""} onChange={(e)=>{
                            if(e.target.checked){
                                this.setState({
                                    showMortgage:false,
                                    showExtension:true
                                })
                            }
                            this.setState({
                                ti1:""
                            })
                        }}/></div></label></span>
                        <span
                            style={{marginLeft:"0.54rem"}}
                        ><label><div
                        style={{background:this.state.ti1==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti1" defaultChecked={this.state.ti1==""?"true":""} onChange={(e)=>{
                            if(e.target.checked){
                                this.setState({
                                    showMortgage:false,
                                    showExtension:false
                                })
                            }
                            this.setState({
                                ti1:""
                            })
                        }}/></div></label></span>
                    </div>
                    <div
                        style={{display:this.state.showMortgage?"":"none",borderTop:"1px solid #ffe7c0"}}
                    >
                        <span></span>
                        <span><label><div
                            style={{background:this.state.ti2==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        >
                        <input type="radio" name="ti2" defaultChecked={this.state.ti2==""?"true":""} onChange={()=>{
                            this.setState({
                                ti2:""
                            })
                        }}/>
                            </div></label></span>
                        <span
                        ><label><div
                        style={{background:this.state.ti2==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti2" defaultChecked={this.state.ti2==""?"true":""} onChange={()=>{
                            this.setState({
                                ti2:""
                            })
                        }}/></div></label></span>
                        <span
                            style={{marginLeft:"0.54rem"}}
                        ><label><div
                        style={{background:this.state.ti2==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti2" defaultChecked={this.state.ti2==""?"true":""} onChange={()=>{
                            this.setState({
                                ti2:""
                            })
                        }}/></div></label></span>
                    </div>
                    <div
                        style={{display:this.state.showExtension?"":"none",borderTop:"1px solid #ffe7c0"}}
                    >
                        <span></span>
                        <span><label><div
                            style={{background:this.state.ti3=="1"?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        >
                        <input type="radio" name="ti3" defaultChecked={this.state.ti3=="1"?"true":""} onChange={()=>{
                            this.setState({
                                ti3:"1"
                            })
                        }}/>
                            </div></label></span>
                        <span
                        ><label><div
                        style={{background:this.state.ti3=="0"?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti3" defaultChecked={this.state.ti3=="0"?"true":""} onChange={()=>{
                            this.setState({
                                ti3:"0"
                            })
                        }}/></div></label></span>
                    </div>
                </div>
                <div className="tips">
                <img src="images/images/icon_05.png" />
                    <span></span>
                </div>
                <div className="list_info">
                    <div
                        // style={{borderTop:"1px solid #ffe7c0"}}
                        // style={{color:"red"}}
                    >
                        <span></span>
                        <span><label><div
                            style={{background:this.state.ti4==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        >
                        <input type="radio" name="ti4" defaultChecked={this.state.ti4==""?"true":""} onChange={()=>{
                            this.setState({
                                ti4:""
                            })
                        }}/>
                            </div></label></span>
                        <span
                        ><label><div
                        style={{background:this.state.ti4==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti4" defaultChecked={this.state.ti4==""?"true":""} onChange={()=>{
                            this.setState({
                                ti4:""
                            })
                        }}/></div></label></span>
                        <span
                            style={{marginLeft:"0.54rem"}}
                        ><label><div
                        style={{background:this.state.ti4==""?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti4" defaultChecked={this.state.ti4==""?"true":""} onChange={()=>{
                            this.setState({
                                ti4:""
                            })
                        }}/></div></label></span>
                    </div>
                </div>
                <div className="tips">
                <img src="images/images/icon_05.png" />
                    <span></span>
                </div>
                <div className="list_info">
                    <div>
                        <span></span>
                        <span><label><div
                            style={{background:this.state.ti5=="1"?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        >
                        <input type="radio" name="ti5" defaultChecked={this.state.ti5=="1"?"true":""} onChange={()=>{
                            this.setState({
                                ti5:"1"
                            })
                        }}/>
                            </div></label></span>
                        <span
                        ><label><div
                        style={{background:this.state.ti5=="0"?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti5" defaultChecked={this.state.ti5=="0"?"true":""} onChange={()=>{
                            this.setState({
                                ti5:"0"
                            })
                        }}/></div></label></span>
                    </div>
                </div>
                <div className="tips">
                <img src="images/images/icon_05.png" />
                    <span></span>
                </div>
                <div className="list_info">
                    <div>
                        <span></span>
                        <span><label><div
                            style={{background:this.state.ti6=="1"?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        >
                        <input type="radio" name="ti6" defaultChecked={this.state.ti6=="1"?"true":""} onChange={()=>{
                            this.setState({
                                ti6:"1"
                            })
                        }}/>
                            </div></label></span>
                        <span
                        ><label><div
                        style={{background:this.state.ti6=="0"?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti6" defaultChecked={this.state.ti6=="0"?"true":""} onChange={()=>{
                            this.setState({
                                ti6:"0"
                            })
                        }}/></div></label></span>
                    </div>
                </div>
                <div className="tips">
                <img src="images/images/icon_05.png" />
                    <span></span>
                </div>
                <div className="list_info">
                    <div>
                        <span></span>
                        <Picker extra=""
                                    
                                    data={[{label:"0-3000/",value:"0-3000/"},{label:"3001-6000/",value:"3001-6000/"},{label:"6001-9000/",value:"6001-9000/"},{label:"9001-12000/",value:"9001-12000/"},{label:"12000/",value:"12000/"}]}
                                    cols="1" 
                                    value={this.state.ti8}                                  
                                    onOk={e => {this.setState({ti8:e})}}
                                    onDismiss={e => console.log('dismiss', e)}
                                    >
                                    <List.Item
                                        style={{width:"4rem"}}
                                    ></List.Item>
                        </Picker>
                    </div>
                </div>
                <div className="tips">
                <img src="images/images/icon_05.png" />
                    <span></span>
                </div>
                <div className="list_info">
                    <div>
                        <span></span>
                        <span><label><div
                            style={{background:this.state.ti7=="1"?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        >
                        <input type="radio" name="ti7" defaultChecked={this.state.ti7=="1"?"true":""} onChange={()=>{
                            this.setState({
                                ti7:"1"
                            })
                        }}/>
                            </div></label></span>
                        <span
                        ><label><div
                        style={{background:this.state.ti7=="0"?"url(images/images/small-2.png) 0% 0% /100%":"url(images/images/small-1.jpg) 0% 0% /100%"}}
                        ><input type="radio"  name="ti7" defaultChecked={this.state.ti7=="0"?"true":""} onChange={()=>{
                            this.setState({
                                ti7:"0"
                            })
                        }}/></div></label></span>
                    </div>
                </div>
                <div className="submit">
                    <button  onClick={this.btn}></button>
                </div>
            </div>
        )
    }
})


