


var isCardNo=function(card) 
{ 
// 1518151817X 
		var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/; 
		if(reg.test(card) === false) 
		{ 
		return false; 
		}else{
			return true
		}
        }
export default isCardNo;


