import {Button} from "antd-mobile";


export default React.createClass({
    getInitialState(){
        return {
            // left:"primary",
            current:"1",
            page:"1"
        }
    },
    dayleft(){
        this.setState({
            page:"1"
        })
    },
    dayright(){
        this.setState({
            page:"2"
        })
    },
    btnLeft(e){
        // console.log(e);
        // console.log(e.target)
        // this.setState({
        //     left:"primary",
        //     right:""
        // })
        this.setState({
            current:"1"
        })
    },
    btnRight(){
        // this.setState({
        //     left:"",
        //     right:"primary"
        // })
        this.setState({
            current:"2"
        })
    },
    render(){
        const index=this.state.current;
        const page=this.state.page;
        return (
            <div className="operating">
                <p className="op_title"></p>
                <p className="op_type"></p>
                <div className="money">
                    <div className="left">
                        <Button
                        onClick={this.btnLeft}
                        className={index=="1"?"active":""}
                        >1000</Button>
                    </div>
                    <div className="right">
                        <Button
                        className={index=="2"?"active":""}
                        onClick={this.btnRight}
                        >2000</Button>
                    </div>
                </div>
                <p className="op_type"></p>
                <div className="day">
                    <Button
                    inline="true"
                    onClick={this.dayleft}
                    className={page=="1"?"active":""}
                    >7</Button>
                    <Button
                    inline="true"
                    className={page=="2"?"active":""}
                    onClick={this.dayright}
                    >14</Button>
                </div>
                <div className="submit">
                    <Button
                    type="primary"
                    ></Button>
                </div>
                <p className="application"><span></span><a href="javascript:;"></a></p>
            </div>
        )
    }
})


